  font-size: 18px;
  font-weight: 600;
  color: #384158;
  transition: all 200ms ease;
}
.main_nav li a:hover,
.main_nav li.active a {
  color: #14bdee;
}
.main_nav li.active::after {
  display: block;
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #14bdee;
  content: "";
}
.search_button {
  margin-left: 46px;
