  transition: all 200ms ease;
}
.search_button:hover i,
.shopping_cart:hover i {
  color: #14bdee;
}
.header_search_form {
  display: block;
  position: relative;
  width: 40%;
}
.header_search_container {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  background: #14bdee;
  z-index: -1;
  opacity: 0;
  transition: all 400ms ease;
}
.header_search_container.active {
  bottom: -73px;
  opacity: 1;
}
.header_search_content {
  width: 100%;
  height: 73px;
}
.search_input {
  width: 100%;
  height: 40px;
  border: none;
  outline: none;
  padding-left: 20px;
}
.header_search_button {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 100%;
  border: none;
